body{
    font-family: 'Roboto', sans-serif;
}
h1 {
    color: #ED1E79;
}
textarea:focus, input:focus{
    outline: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    
}

.input_container {
    
    height: 20%;
    margin: auto 50px;
    margin-bottom: 1%;
    margin-top: 2%;
    
}

.simple-text {
    margin: 0;
    margin-top: 20px;
}

.input_grid {
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    justify-content: center
}

.input_combo {
    display: flex;
}

.input_number {
    width: 4rem;
    border: 0;
    font-size: 36px;
    text-align: center;
    color: #ED1E79;
    transition: border-color 0.2s;
    border-bottom: 2px solid
}
.input_kronor {
    width: 9rem;
    border: 0;
    font-size: 2.3rem;
    text-align: center;
    color: #ED1E79;
    transition: border-color 0.2s;
    border-bottom: 2px solid
}

.side-text {
    color: #ED1E79;
}
