body{
    font-family: 'Roboto', sans-serif;
}

.content-table {
    margin-left: auto;
    margin-right: auto;
    width: 80vh;   
    font-size: 0.9em;
    min-width: 400px;
    border-radius: 10px;
    overflow: hidden;
}

.content-table thead tr {
    background-color: #ED1E79;
    color: #fff;
    text-align: left;
    font-weight: bold;
    
}

.content-table th,
.content-table td {
    padding: 12px 15px;
}



.content-table tbody tr:nth-of-type(even) {
    background-color: rgb(245, 245, 245);
}

.content-table tbody tr:last-of-type {
    border-bottom: 2px solid #ED1E79;
}

.content-table .line-class:hover{
    background-color: #ED1E79;
    color: #fff;
    transition: 0.1s ease-in-out;
}

.Grattis{

    margin-top: 10vh;
    font-size: 20px;
    display: flex;
    justify-content: center;
    text-align: center;
    align-items: center;
    justify-content: center
}